import React, { useEffect, useState, useRef } from "react";
import { MapContainer, TileLayer, Marker, Popup, GeoJSON } from "react-leaflet";
import "leaflet/dist/leaflet.css";
import { Card, Input } from "../components/ui"; // Lis valitsemallesi Select-komponentti
import L from 'leaflet'; // Tuo Leaflet-kirjasto
import { useRadioGroup, Box, ButtonGroup, Button, FormControl, Checkbox, FormControlLabel, FormLabel, Radio, RadioGroup, Select, InputLabel, MenuItem, Slider, TextField } from '@mui/material';

const Building = () => {
  const [area, setArea] = useState(4486); // Pinta-ala (m)
  const [asuntoja, setAsuntoja] = useState(0); 
  const [kellaripit, setKellaripit] = useState(0);
  const [kellarilev, setKellarilev] = useState(0); 
  const [ullakkolev, setUllakkolev] = useState(0);
  const [ullakkopit, setUllakkopit] = useState(0);
  const [asuinkerrokset, setAsuinkerrokset] = useState(0);
  const [asuinkerrospit, setAsuinkerrospit] = useState(0);
  const [asuinkerroslev, setAsuinkerroslev] = useState(0);
  const [hissinkonehuonepit, setHissinkonehuonepit] = useState(0);
  const [hissinkonehuonelev, setHissinkonehuonelev] = useState(0);
  const [puutajavalmistunutennen60, setPuutajavalmistunutennen60] = useState(false);
  const [hissi, setHissi] = useState(true);
  const [yli80viiva120, setYli80viiva120] = useState(0);
  const [yli120, setYli120] = useState(0);
  const [kerroslukukellarimukana, setKerroslukukellarimukana] = useState(0);
  const [kerroksia3, setKerroksia3] = useState(true);
  const [kerroksia4, setKerroksia4] = useState(true);
  const [kerroksia5, setKerroksia5] = useState(true);
  const [kerroksia6, setKerroksia6] = useState(true);
  const [kerroksia7, setKerroksia7] = useState(true);
  const [kerroksia8, setKerroksia8] = useState(true);
  const [ikaalennus, setIkaalennus] = useState(8);
  const [kiinteistovero, setKiinteistovero] = useState(0.5);
  const [rakennuskustannusindeksi, setRakennuskustannusindeksi] = useState(0);
  const [selectedRegion, setSelectedRegion] = useState(null); // Valittu alue
  const [price, setPrice] = useState(null); // Laskettu hinta
  const [typeprice, setTypeprice] = useState(null);
  const [geojsonData, setGeojsonData] = useState(null);
  const [geojsonKunnat, setGeojsonKunnat] = useState(null);
  const [selectedKunta, setSelectedKunta] = useState(null); // Valittu alue
  const mapRef = useRef(null); // mapRef sijoitettu ennen returnia
  const [priceType, setPriceType] = useState("kerros"); // Oletushintatyyppi on asuinalue
  const [calculationBasis, setCalculationBasis] = useState("pinta-ala"); // Laskentaperuste: "pinta-ala" tai "rakennusoikeus"
  const [selectedBtn, setSelectedBtn] = React.useState(-1);

  /*useEffect(() => {
    
    fetch("./data/postinumerot2025webmercator.geojson")
      .then((response) => response.json())
      .then((data) => setGeojsonData(data))
      .catch((error) => console.error("Virhe GeoJSON-tiedoston lataamisessa:", error));

    fetch("../../data/kuntarajat.geojson")
      .then((response2) => response2.json())
      .then((data2) => setGeojsonKunnat(data2))
      .catch((error2) => console.error("Virhe kuntarajojen lataamisessa:", error2));
  }, []);
*/
  useEffect(() => {
    if (selectedRegion !== null) {
      setTypeprice(getPriceFromFeature(selectedRegion));
    }
  }, [priceType, selectedRegion]);

  const geoJsonStyle = (feature) => {
    const hinta = feature.properties.he_vakiy;
    let color;
    if (hinta > 4000) {
      color = "red";
    } else if (hinta > 2000) {
      color = "orange";
    } else {
      color = "green";
    }

    return {
      color: "black",
      fillColor: color,
      fillOpacity: 0.1,
      weight: 1,
    };
  };

  const geoJsonStyleKunnat = (feature) => {
    const hinta = feature.properties.landarea;
    let color;

    if (hinta > 4000) {
      color = "blue";
    } else if (hinta > 2000) {
      color = "yellow";
    } else {
      color = "red";
    }

    return {
      color: "red",
      fillColor: color,
      fillOpacity: 0.1,
      weight: 3,
    };
  };

  const onEachFeature = (feature, layer) => {
    layer.on({
      click: () => {
        setSelectedRegion(feature.properties);
      },
    });

    if (feature.properties && feature.properties.nimi) {
      layer.bindPopup(
        `<strong>Alue:</strong> ${feature.properties.nimi}<br />
         <strong>Hinta:</strong> ${feature.properties.he_vakiy} /m`
      );
    }
  };

  const onEachKunta = (feature, layer) => {
    if (feature.geometry.type === "Polygon" || feature.geometry.type === "MultiPolygon") {
      const bounds = layer.getBounds();
      const center = bounds.getCenter();

      const icon = L.divIcon({
        html: `<div class="map-label">${feature.properties.namefin}</div>`,
        className: 'map-label',
        iconSize: null,
        iconAnchor: [0, 0]
      });

      if (mapRef.current) {
        L.marker(center, { icon: icon }).addTo(mapRef.current);
      }
    }
  };

  const getPriceFromFeature = (feature) => {
    if (priceType === "residential") {
      return feature.he_naiset || 0;
    } else if (priceType === "industrial") {
      return feature.he_vakiy || 0;
    } else if (priceType === "commercial") {
      return feature.he_miehet || 0;
    }
    return 0;
  };

  const perusarvo=769.04
  const kerros3=40.05
  const kerros4=19.84
  const kerros5=0
  const kerros6=19.84
  const kerros7=40.05
  const kerros8=59.92
  const calculatePrice = () => {
    if (area > 0) {
      if (selectedBtn===3) {
        const totalPrice = ((area * perusarvo+area*kerros3)+((area * perusarvo+area*kerros3)*ikaalennus/100))*kiinteistovero/100;
        setPrice(totalPrice);
      }
      if (selectedBtn===4) {
        const totalPrice = ((area * perusarvo+area*kerros4)-((area * perusarvo+area*kerros4)*ikaalennus/100))*kiinteistovero/100;
        setPrice(totalPrice);
      }
      if (selectedBtn===5) {
        const totalPrice = ((area * perusarvo-area*kerros5)-((area * perusarvo-area*kerros5)*ikaalennus/100))*kiinteistovero/100;
        setPrice(totalPrice);
      }
      if (selectedBtn===6) {
        const totalPrice = ((area * perusarvo-area*kerros6)-((area * perusarvo-area*kerros6)*ikaalennus/100))*kiinteistovero/100;
        setPrice(totalPrice);
      }
      if (selectedBtn===7) {
        const totalPrice = ((area * perusarvo-area*kerros7)-((area * perusarvo-area*kerros7)*ikaalennus/100))*kiinteistovero/100;
        setPrice(totalPrice);
      }
      if (selectedBtn===8) {
        const totalPrice = ((area * perusarvo-area*kerros8)-((area * perusarvo-area*kerros8)*ikaalennus/100))*kiinteistovero/100;
        setPrice(totalPrice);
      }
      
    } else {
      alert("Osa tiedoista puuttuu");
    }
  };


  const buttons = [
    <Button key="kolme" variant="contained" color={selectedBtn === 3 ? "secondary" : "primary"} onClick={()=>setSelectedBtn(3)}>Kolme</Button>,
    <Button key="nelja" variant="contained" color={selectedBtn === 4 ? "secondary" : "primary"} onClick={()=>setSelectedBtn(4)}>Nelj</Button>,
    <Button key="viisi" variant="contained"color={selectedBtn === 5 ? "secondary" : "primary"} onClick={()=>setSelectedBtn(5)}>Viisi</Button>,
    <Button key="kuusi" variant="contained" color={selectedBtn === 6 ? "secondary" : "primary"} onClick={()=>setSelectedBtn(6)}>kuusi</Button>,
    <Button key="seitseman" variant="contained" color={selectedBtn === 7 ? "secondary" : "primary"} onClick={()=>setSelectedBtn(7)}>Seitsemn</Button>,
    <Button key="kahdeksan" variant="contained" color={selectedBtn === 8 ? "secondary" : "primary"} onClick={()=>setSelectedBtn(8)}>Kahdeksan tai enemmn</Button>,
  ];
  
  return (
    <div style={{ padding: "20px", textAlign: "center" }}>
     
        <div style={{ width: "100%", height: "600px" }}>
          <h1 className="text-2xl font-bold mb-4">Rakennuksen hinnan laskuri</h1>
          <Card className="w-full max-w-lg p-4 mb-4">
          <FormControl fullWidth>
            <InputLabel id="demo-simple-select-label">Rakennustyyppi</InputLabel>
            <Select
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                value={priceType}
                label="Age"
                onChange={(e) => setPriceType(e.target.value)}
            >
                <MenuItem value="kerros">Kerrostalo</MenuItem>
                <MenuItem value="teollisuus">Teollisuuslaitos</MenuItem>
                <MenuItem value="yritys">Yritys</MenuItem>
                <MenuItem value="rivi">Rivitalo</MenuItem>
                <MenuItem value="pientalo">Pientalo</MenuItem>
            </Select>
            </FormControl>
                <br />
           
            <div className="mb-4">
              <label className="block text-sm font-medium mb-2">Kokonaispinta-ala (m):<br /> 
                <TextField
                id="outlined-number"
                label="m"
                type="number"
                slotProps={{
                    inputLabel: {
                    shrink: true,
                    },
                }}
                value={area}
                onChange={(e) => setArea(Number(e.target.value))}
                /></label>
              <Slider
                size="small"
                defaultValue={50}
                min={0}
                max={10000}
                aria-label="Small"
                valueLabelDisplay="auto"
                onChange={(e) => setArea(Number(e.target.value))}
                value={area}
                step="1"
                />
                              
            </div>
            <div className="mb-4">
              <label className="block text-sm font-medium mb-2">Ikalennus:<br /> 
                <TextField
                id="outlined-number"
                label="%"
                type="number"
                slotProps={{
                    inputLabel: {
                    shrink: true,
                    },
                }}
                value={ikaalennus}
                onChange={(e) => setIkaalennus(Number(e.target.value))}
                /></label>
              <Slider
                size="small"
                defaultValue={8}
                min={0}
                max={100}
                aria-label="Small"
                valueLabelDisplay="auto"
                onChange={(e) => setIkaalennus(Number(e.target.value))}
                value={ikaalennus}
                step="1"
                />
                              
            </div>
            <div className="mb-4">
              <label className="block text-sm font-medium mb-2">kiinteistvero<br /> 
                <TextField
                id="outlined-number"
                label="%"
                type="number"
                slotProps={{
                    inputLabel: {
                    shrink: true,
                    },
                }}
                value={kiinteistovero}
                onChange={(e) => setKiinteistovero(Number(e.target.value))}
                /></label>
              <Slider
                size="small"
                defaultValue={8}
                min={0}
                max={100}
                aria-label="Small"
                valueLabelDisplay="auto"
                onChange={(e) => setKiinteistovero(Number(e.target.value))}
                value={kiinteistovero}
                shiftStep={0.5}
                step="1"
                />
                              
            </div>
            <div className="mb-4">
              <label className="block text-sm font-medium mb-2">Asuntojen lukumr (kpl):<br /> 
                <TextField
                id="outlined-number"
                label="kpl"
                type="number"
                slotProps={{
                    inputLabel: {
                    shrink: true,
                    },
                }}
                value={asuntoja}
                onChange={(e) => setAsuntoja(Number(e.target.value))}
                /></label>
              <Slider
                size="small"
                defaultValue={50}
                min={0}
                max={500}
                aria-label="Small"
                valueLabelDisplay="auto"
                onChange={(e) => setAsuntoja(Number(e.target.value))}
                value={asuntoja}
                step="1"
                />
                              
            </div>
            <label className="block text-sm font-medium mb-2">Kellari<br /> 
                <TextField
                id="outlined-number"
                label="Pituus m"
                type="number"
                slotProps={{
                    inputLabel: {
                    shrink: true,
                    },
                }}
                value={kellaripit}
                onChange={(e) => setKellaripit(Number(e.target.value))}
                />
                <TextField
                id="outlined-number"
                label="Leveys m"
                type="number"
                slotProps={{
                    inputLabel: {
                    shrink: true,
                    },
                }}
                value={kellarilev}
                onChange={(e) => setKellarilev(Number(e.target.value))}
                />
                  = yhteens {kellarilev*kellaripit} m
              </label>
              <br /><br />
              <label className="block text-sm font-medium mb-2">asuinkerrokset<br /> 
                <TextField
                id="outlined-number"
                label="Pituus m"
                type="number"
                slotProps={{
                    inputLabel: {
                    shrink: true,
                    },
                }}
                value={asuinkerrospit}
                onChange={(e) => setAsuinkerrospit(Number(e.target.value))}
                />
                <TextField
                id="outlined-number"
                label="Leveys m"
                type="number"
                slotProps={{
                    inputLabel: {
                    shrink: true,
                    },
                }}
                value={asuinkerroslev}
                onChange={(e) => setAsuinkerroslev(Number(e.target.value))}
                />
                  = yhteens {asuinkerroslev*asuinkerrospit} m
              </label>
              <br /><br />
              <label className="block text-sm font-medium mb-2">Hissin kone huone<br /> 
                <TextField
                id="outlined-number"
                label="Pituus m"
                type="number"
                slotProps={{
                    inputLabel: {
                    shrink: true,
                    },
                }}
                value={hissinkonehuonepit}
                onChange={(e) => setHissinkonehuonepit(Number(e.target.value))}
                />
                <TextField
                id="outlined-number"
                label="Leveys m"
                type="number"
                slotProps={{
                    inputLabel: {
                    shrink: true,
                    },
                }}
                value={hissinkonehuonelev}
                onChange={(e) => setHissinkonehuonelev(Number(e.target.value))}
                />
                  = yhteens {hissinkonehuonelev*hissinkonehuonepit} m
              </label>
              <br /><br />
              Kantava rakenne puuta ja rakennettu ennen 1960<Checkbox name="puuta" label="puuta" defaultChecked color="success" />
              <br />
              Rakennuksessa hissi<Checkbox label="hissi" defaultChecked color="success" />
              <br />
              Yli 80 m - 120 m<Checkbox name="puuta" label="puuta" defaultChecked color="success" />
              <br/>
              Kerroksia kellari mukaan laskettuna<br />
                <ButtonGroup
                  orientation="vertical"
                  aria-label="Vertical button group"
                >
                  {buttons}
                </ButtonGroup>

              <br /><br />
              {price !== null && (
              <div className="mt-4 text-center">
                <p className="text-lg font-semibold"><h2><b>Laskettu kiinteistvero: {price.toLocaleString()} </b></h2></p>
                <p className="text-xl font-bold"></p>
              </div>
            )}
            <br />
            <Button variant="contained" color="success" onClick={calculatePrice}>
            Laske kiinteistvero
            </Button>
            
          </Card>
        </div>
    </div>
  );
};

export default Building;