// Korvaa Card-komponentti
export const Card = ({ children, className }) => (
  <div className={`bg-white shadow rounded-xl p-4 ${className}`}>{children}</div>
);

// Korvaa Input-komponentti
export const Input = ({ ...props }) => (
  <input
    className="w-full p-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
    {...props}
  />
);

// Korvaa Button-komponentti
export const Button = ({ children, onClick, className }) => (
  <button
    className={`bg-blue-500 text-white px-4 py-2 rounded-lg hover:bg-blue-600 ${className}`}
    onClick={onClick}
  >
    {children}
  </button>
);

export const Select = ({ value, onChange, options }) => (
    
    <select
    value={value}
    onChange={onChange}
    className="block w-full p-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
    >
    {options.map((option) => (
        <option key={option.value} value={option.value}>
        {option.label}
        </option>
    ))}
    </select>
);

