import React, { useEffect, useState, useRef, Suspense } from "react";




const Logout = ({setToken}) => {
useEffect(() => {
    document.title = "Verolaskurit"
    setToken(null)
    localStorage.clear()
  }, [])

  return (
  <div>
      <h1>Verolaskurit</h1>
      <p>Arvioi eri veroluokkien verotulot</p>
    </div>
  );
};

export default Logout;
