import {
  AppBar,
  Toolbar,
  Typography,
  IconButton,
  Drawer,
  Link,
  MenuItem,
} from "@mui/material"
import { makeStyles } from "@mui/styles"
import MenuIcon from "@mui/icons-material/Menu"
import HomeIcon from '@mui/icons-material/Home'
import CorporateIcon from '@mui/icons-material/CorporateFare'
import StartIcon from '@mui/icons-material/Start'
import LandIcon from '@mui/icons-material/Landscape'
import LogoutIcon from '@mui/icons-material/Logout'
import FeedbackIcon from '@mui/icons-material/Feedback'
import ContactMailIcon from '@mui/icons-material/ContactMail'
import VpnKeyIcon from '@mui/icons-material/VpnKey'
import React, { useState, useEffect } from "react"
import { Link as RouterLink } from "react-router-dom"
import ico from "../pics/icon.png"

const useStyles = makeStyles(() => ({
  header: {
    backgroundColor: "#00075B",
    paddingRight: "3px",
    paddingLeft: "3px",
    "@media (max-width: 900px)": {
      paddingLeft: 0,
    },
  },
  logo: {
    fontFamily: "Work Sans, sans-serif",
    fontWeight: 600,
    color: "#000000",
    textAlign: "left",
  },
  menuButton: {
    fontFamily: "Open Sans, sans-serif",
    fontWeight: 700,
    size: "15px",
    marginLeft: "2px",
    color:"#999999",
  },
  toolbar: {
    display: "flex",
    justifyContent: "space-between",
    backgroundColor: "#00075B"
  },
  drawerContainer: {
    padding: "10px 10px",
  },
}));

export default function Headerpublic() {
  // eslint-disable-next-line
  const { header, logo, menuButton, toolbar, drawerContainer } = useStyles();
  
const headersData = [
  {
    label: "Alku",
    href: "/",
    icon: <StartIcon />
  },
  {
    label: "Maa",
    href: "/land",
    icon: <LandIcon />
  },
  {
    label: "Rakennus",
    href: "/building",
    icon: <CorporateIcon />
  },
  {
    label: "Kirjaudu ulos",
    href: "/logout",
    icon: <LogoutIcon />
  }
];

  const [state, setState] = useState({
    mobileView: false,
    drawerOpen: false,
  });

  const { mobileView, drawerOpen } = state;

  useEffect(() => {
    const setResponsiveness = () => {
      return window.innerWidth < 900
        ? setState((prevState) => ({ ...prevState, mobileView: true }))
        : setState((prevState) => ({ ...prevState, mobileView: false }));
    };

    setResponsiveness();

    window.addEventListener("resize", () => setResponsiveness());
  }, []);

  const displayDesktop = () => {
    return (
      <Toolbar className={toolbar}>
        <Link
          {...{
            component: RouterLink,
            to: '/',
            style: { textDecoration: "none" },
          }}
        >
          <div style={{position: 'absolute', left: '1%', top: '10px'}  }>{Logo}</div>
          <div style={{position: 'absolute', left: '4%', top: '15px',color: '#D1DBE2', fontSize: '20px', fontFamily:'Arial black' }  }>Verolaskurit</div>
        </Link>
        <div>{getMenuButtons()}</div>
      </Toolbar>
    );
  };

  const displayMobile = () => {
    const handleDrawerOpen = () =>
      setState((prevState) => ({ ...prevState, drawerOpen: true }));
    const handleDrawerClose = () =>
      setState((prevState) => ({ ...prevState, drawerOpen: false }));

    return (
      <Toolbar>
        <IconButton
          {...{
            edge: "start",
            color: "inherit",
            "aria-label": "menu",
            "aria-haspopup": "true",
            onClick: handleDrawerOpen,
          }}
        >
         <div style={{position: 'absolute', left: '10px',color: '#C1DBE2'}  }><MenuIcon /></div> 
        </IconButton>
        <Link
          {...{
            component: RouterLink,
            to: '/',
            style: { textDecoration: "none" },
          }}
        >
          <div style={{position: 'absolute', left: '33%', top: '10px'}  }>{Logo}</div>
          <div style={{position: 'absolute', left: '44%', top: '15px',color: '#D1DBE2', fontSize: '20px', fontFamily:'Arial black' }  }>Verolaskurit</div>
        </Link>
        <Drawer
          {...{
            anchor: "left",
            open: drawerOpen,
            onClose: handleDrawerClose,
          }}
        >
          <div className={drawerContainer}>{getDrawerChoices()}</div>
        </Drawer>

        <div style={{position: 'absolute', right: '10px',}  }>
        <IconButton
          {...{
            color: "inherit",
            to: '/login',
            component: RouterLink,
            className: menuButton,
            size: "small"
          }}
        >
          <VpnKeyIcon />
        </IconButton></div>
      </Toolbar>
    );
  };

  const getDrawerChoices = () => {
    return headersData.map(({ label, href, icon }) => {
      return (
        <Link
          {...{
            component: RouterLink,
            to: href,
            color: "inherit",
            style: { textDecoration: "none" },
            key: label,
          }}
        >
          <MenuItem>{label}</MenuItem>
        </Link>
      );
    });
  };

  const Logo = (
    <div>
    <img src={ico} alt='Verolaskurit'/>
    </div>
  );

  const getMenuButtons = () => {
    return headersData.map(({ label, href, icon }) => {
      return (
        <IconButton
          {...{
            key: label,
            color: "inherit",
            to: href,
            component: RouterLink,
            className: menuButton,
            size: "small"
          }}
        >
          {icon}
          <div>{label}</div>
        </IconButton>
      );
    });
  };

  return (
    <header>
      <AppBar className={header}>
        {mobileView ? displayMobile() : displayDesktop()}
      </AppBar>
    </header>
  );
}